
























































































import { computed, ref } from "vue";
import { useCool, useStore, useWx } from "/@/cool";
import IconPhone from "./static/icon/phone.png";
import IconWx from "./static/icon/wx.png";

const { ui, service, router } = useCool();

// 应用缓存
const { app, user } = useStore();

// 微信
const wx = useWx();

// 是否同意
const agree = ref<boolean>(true);

// 加载状态
const loading = ref<boolean>(false);

// 手机号
const phone = ref<string>("");

// 手机号是否正确
const phoneValid = computed(() => /^(?:(?:\+|00)86)?1[3-9]\d{9}$/.test(phone.value));

// 登录平台
const platforms = ref<any[]>([
	{
		label: "通过手机登录",
		value: "phone",
		icon: IconPhone,
	},
	// {
	// 	label: "通过微信登录",
	// 	value: "wx",
	// 	icon: IconWx,
	// 	hidden: !wx.hasApp(),
	// },
]);

// 登录方式
const mode = ref<string>("phone");

// 检测
function check(callback: Function) {
	if (!agree.value) {
		return ui.showToast("请先勾选同意后再进行登录");
	}

	callback();
}

// 登录
async function nextLogin(req: Promise<string>) {
	check(() => {
		req.then(async (token: string) => {
			// 设置token
			user.setToken(token);

			// 设置用户信息
			await user.get();

			// 登录跳转
			router.nextLogin();
		}).catch((err) => {
			ui.showTips(err.message);
		});
	});
}

// 手机号登录
function phoneLogin() {
	check(async function () {
		loading.value = true;

		await service.user.user
			.getCode({
				phone: phone.value,
			})
			.then(() => {
				router.push({
					path: "/pages/user/captcha",
					mode: "redirectTo",
					query: {
						phone: phone.value,
					},
				});
			})
			.catch((err) => {
				ui.showTips(err.message);
			});

		loading.value = false;
	});
}

// 微信登录
function wxLogin() {



















	wx.miniLogin();

}

// 切换模式
function changeMode(value: string) {
	mode.value = value;
}

// 文案
function toText(name: string) {
	router.push({
		path: "/pages/comm/text",
		query: {
			name,
		},
	});
}
